import * as React from 'react';

export class StateDemo extends React.Component<{},{}> {
    constructor(public props){
        super(props);
    }
    render() {
        return (
            <section>
                <p>State Params: {this.props.params.stateParam}</p>
                <p>Redux state demo</p>
            </section>
        );
    }
}



