import {Action} from '../config/action';
export const TEST_ACTION = "TEST_ACTION";
export type TEST_ACTION = {foo: number, message:string};

export function doTestAction(message:string):Action<TEST_ACTION>{
    return {
        type: TEST_ACTION,
        payload:{
            foo: 123,
            message: message
        }
    }
}


