import {createStore, applyMiddleware, compose, Middleware, combineReducers} from 'redux';
import {browserHistory} from "react-router";
import {routerMiddleware, routerReducer} from 'react-router-redux';


const rootReducer = combineReducers(
    getReducers(require.context('../',true,/.*\.reducer\.tsx$/))
);

export function configureStore(initialState?) {
    const store = createStore(
        combineReducers({
            rootReducer,
            routing: routerReducer
        }),
        initialState,
        compose(
            applyMiddleware(...getMiddleware())
        ));

    return store;
}

function getMiddleware(): Middleware[] {
    let middleware = [
        routerMiddleware(browserHistory)
    ];

    return middleware;
}

function getReducers(reducerContext){
    return reducerContext.keys()
        .filter( reducer => !~reducer.indexOf('.ts'))
        .reduce((reducers,reducer)=>{
            return {
                ...reducers,
                [reducer.substr(2)]: reducerContext(reducer).default
            }
        },{});
}


